
// Utility
// ––––––––––––––––––––––––––––––––––––––––––––––––––

// Clear a float with .clearfix
%clearfix {

  &:after {
    clear: both;
    content: ' '; // The space content is one way to avoid an Opera bug.
    display: table; } }

// Float either direction
%float-left {
  float: left; }

%float-right {
  float: right; }


// My Extensions

%sr-only {
  border: 0;
  clip: rect(0,0,0,0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

%full-width {
  width: 100%; }
